<template>
  <div class="container">
    <div class="video-info">
      <div>
        <img :src="[video.url]" alt="" />
      </div>
      <div>
        <h1>{{ video.title }}</h1>
        <p>{{ video.descriptionfull }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Video",
  data() {
    return {
      video: [],
    };
  },
  methods: {
    async getInfo() {
      const info = await fetch(
        `http://localhost:3000/videos/${this.$route.params.id}`
      );
      const data = await info.json();
      this.video = data;
      console.log(this.video);
    },
  },
  created() {
    this.getInfo();
  },
};
</script>

<style>
.video-info {
  display: flex;
  align-items: center;
}
.container {
  width: 1200px;
  margin: 0 auto;
}
</style>