<template>
  <div class="form-container">
    <form @submit.prevent="addVideo">
      <div>
        <label for="titulo">Ingrese Titulo</label>
        <input id="titulo" type="text" v-model="video.title" />
      </div>
      <div>
        <label for="description">Ingrese description corta</label>
        <input id="description" type="text" v-model="video.descriptionshort" />
      </div>
      <button class="btn-editar">Agregar</button>
    </form>
  </div>
</template>

<script>
export default {
  name: "Form",
  data() {
    return {
      video: {
        title: "",
        url:
          "https://i.pinimg.com/originals/fd/5e/66/fd5e662dce1a3a8cd192a5952fa64f02.jpg",
        descriptionshort: "",
        descriptionfull:
          "Lorem ipsum dolor sit, amet consectetur adipisicing elit. Doloremque, id! Minima ipsa dolor iusto in fuga debitis quis quibusdam dolorum perspiciatis explicabo placeat nesciunt hic, est quaerat ex id eum?",
      },
    };
  },
  methods: {
    async addVideo() {
      await fetch("http://localhost:3000/videos", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(this.video),
      })
        .then((res) => res.json())
        .catch((error) => console.error("Error:", error))
        .then((response) => console.log("Success:", response));

      this.$router.push({ name: "Home", query: { redirect: "/" } });
    },
  },
};
</script>

