<template>
  <di class="form-container">
    <form @submit.prevent="updateInfo">
      <div>
        <label for="titulo">Nuevo Titulo</label>
        <input id="titulo" type="text" v-model="title" />
      </div>
      <button class="btn-editar">Editar</button>
    </form>
  </di>
</template>

<script>
export default {
  data() {
    return {
      title: "",
    };
  },
  methods: {
    async updateInfo() {
      await fetch(`http://localhost:3000/videos/${this.$route.params.id}`, {
        method: "PATCH",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify({ title: this.title }),
      });
      this.$router.push({ name: "Home", query: { redirect: "/" } });
    },
  },
};
</script>

<style>
.form-container {
  background-color: rgba(238, 90, 64, 0.863);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.btn-editar {
  margin-top: 20px;
  padding: 15px 60px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  color: black;
}
</style>